.footer {
    width: 100%;
    background-color:rgba(0, 0, 0, 0.582);
    position:relative;
    z-index:99;
    font-size: 8vh;
    padding-bottom: 3vh;
    display:flex;
    flex-direction:row;
    flex-wrap: wrap;
    justify-content:space-evenly;
    height: 51vh;
    &__credits {
        width: max-content;
        margin-top: 1vw;
        font-size: 2vh;
        padding: 1vh;
    }
    &__icon-link {
        color:white;
        width: 33%;
        text-align:center;
        margin-top: 3vw;
        margin: 2vw 0;
    }
    &__icon-text {
        font-size: 4vh;
        width: 33%;
    }
}
