.header {
    width: 100%;
    height: 7.5vh;
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    z-index: 99;
    position: absolute;
    padding-top: 5vh;
    background: rgba(5, 5, 5, 0.315);
    &__logo {
        width: 20vh;
        height: 12vh;
        /*box-shadow: 3px 3px 5px white;*/
        filter: saturate(2.9);
        position:absolute;
        object-fit:contain;
        left: 45vw;
    }
    
}