.section-card {
    position:relative;
    width: 100%;
    display:flex;
    flex-direction:row;
    justify-content:flex-start;
    background:darken(rgb(2, 117, 121), 10%);
    color:rgb(255, 255, 255);
    margin: 0 0 ;
    z-index:99;
    /*padding: 7vh 0;*/

        &:nth-child(even) {
        flex-direction: row-reverse;
        background:rgb(34, 34, 34);
        color:rgb(255, 255, 255);
        }
    &__image {
        width: 32vw;
        height: 34vw;
        object-fit:cover;
        filter: saturate(0.9);
        &:hover {
            filter: saturate(1.2);
        }
    }
    &__text-container {
        padding: 1vh;
        height: 20vh;
        width: 65vw;
        display:flex;
        flex-direction:row;
        flex-wrap:wrap;
        align-items:flex-start;
        align-content:flex-start;
        
        position:relative;
        & > * {
            width:100%;
        }
        & i {
            font-family: 'Font Awesome 5 Free';
            margin: 1vh 14%;
            font-weight: 600;
            border-radius: 50%;
            font-size: 6vh;
            width: 6vh;
            height:6vh;
            display:flex;
            justify-content:center;
            align-items:center;
        }
    }
    &__title {
        font-weight: lighter;
        font-size: 6vh;
        margin: 0;
        position:absolute;
        top: 3vh;
        z-index:99;
        background: rgba(255, 255, 255, 0.918);
        width:max-content;
        color: black;
        box-shadow: 3px 3px 3px rgb(0, 0, 0);
        padding:0;
        width: 50vh;
        margin: 0 5vh;
    }
    &__subtitle {
        width: 100vw;
        font-size: 4vh;
        line-height: 10vh;
        display:flex;
        flex-direction:row;
        justify-content:space-evenly;
        align-items:center;
        text-align:center;
        margin-top: 3vh;
        font-weight: normal;
    }
    &__content {
        display:flex;
        flex-direction: column;
        flex-wrap:wrap;
        justify-content: flex-start;
        height: 35vh;
        align-items:flex-start;

        & > * {
            margin: 1vh 0;
            font-size: 1.7vh;
            font-weight: 800;
            width: 30%;
            margin: 2vh;
            text-align:justify;
        }
    }
}