.App {
  text-align: center;
  font-family: 'Jost';
  color:white;

}

.home {
    height: 100vh;
    width: 100%;
  &__image {
    width: 100%;
    height: 130vh;
    object-fit: cover;
    filter: saturate(1.1);
    position:sticky;
    top:-30vh;
    left:0;
    z-index:2;
    margin-bottom: -50vh;

  }
  &__activity-image {
    width: 100%;
    height: 95vh;
    object-fit: cover;
    filter: saturate(1.3);
    position:absolute;
    bottom:-40vh;
    left:0;
    z-index:0;
  }
  &__title-container {
    position: sticky;
    right: 10vh;
    bottom: 25vh;
    height: 40vh;
    width: 100vw;
    z-index: 99;
    display:flex;
    align-items:flex-end;
    padding-bottom: 2vh
  }
  &__title {
    
    height: 29vh;
    width:100%;;
    display:flex;
    flex-direction:column;
    flex-wrap:wrap;
    align-items:flex-start;
    z-index:99;
    font-family: 'Cabin Sketch';

    
    &__element {
        margin: 0 7vh;
        width: max-content;
        text-align:right;
        font-size: 10vh;
        color: white;
        font-weight: 200;
        text-shadow: 4px 4px 4px black;

    }
  }
  &__webee {
    height: 40vh;
    width:100%;;
    display:flex;
    flex-direction:column;
    flex-wrap:wrap;
    align-items:flex-end;
    z-index:99;
    object-fit:contain;
    -webkit-filter: drop-shadow(5px 5px 5px #222222);
    filter: drop-shadow(5px 5px 5px #222222);
}
}
